import React from "react";
import { Link } from "react-router-dom";
import { useAuth } from "./security/AuthContext";
import { useTranslation } from "react-i18next";
import { Navbar, Nav, NavDropdown, Container } from 'react-bootstrap';

function HeaderComponent() {
  const authContext = useAuth();
  const isAuthenticated = authContext.isAuthenticated;
  const username = authContext.username;

  // useTranslation 훅 사용
  const { t, i18n } = useTranslation();

  function logout() {
    authContext.logout();
  }

  // 언어 변경 함수
  const changeLanguage = (lang) => {
    i18n.changeLanguage(lang);
  };

  return (
    <header className="border-bottom border-light border-5 mb-5 p-2">
      <Container>
        <Navbar expand="lg">
          <Navbar.Brand href="http://43.201.23.1:3000/" className="ms-2 fs-6 fw-bold text-black">
            Yoon's Library-App Link
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              {isAuthenticated && (
                <>
                  <Nav.Link as={Link} to={`/welcome/${username}`}>{t('home')}</Nav.Link>
                  <Nav.Link as={Link} to="/todos">{t('todos')}</Nav.Link>
                </>
              )}
            </Nav>
            <Nav>
              <NavDropdown title={t('language')} id="language-nav-dropdown">
                <NavDropdown.Item onClick={() => changeLanguage("en")} active={i18n.language === 'en'}>
                  English
                </NavDropdown.Item>
                <NavDropdown.Item onClick={() => changeLanguage("ko")} active={i18n.language === 'ko'}>
                  한국어
                </NavDropdown.Item>
              </NavDropdown>
              {!isAuthenticated && (
                <Nav.Link as={Link} to="/login">{t('login')}</Nav.Link>
              )}
              {isAuthenticated && (
                <Nav.Link as={Link} to="/logout" onClick={logout}>{t('logout')}</Nav.Link>
              )}
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </Container>
    </header>
  );
}

export default HeaderComponent;